import { useEffect, useRef } from "react";
import { motion } from "framer-motion";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { ShieldCheck, LockKeyhole, FileText } from "lucide-react";
import ChatWidget from "../../components/ChatWidget";
import Navigation from "../../components/Navigation";

// Register ScrollTrigger
gsap.registerPlugin(ScrollTrigger);

export default function PrivacyPage() {
  const sectionRefs = useRef([]);

  // Scroll animation using GSAP
  useEffect(() => {
    sectionRefs.current.forEach((el, i) => {
      if (el) {
        gsap.fromTo(
          el,
          { opacity: 0, y: 50 },
          {
            opacity: 1,
            y: 0,
            duration: 0.8,
            delay: i * 0.1,
            ease: "power2.out",
            scrollTrigger: {
              trigger: el,
              start: "top 85%",
              toggleActions: "play none none reverse",
            },
          },
        );
      }
    });
  }, []);

  return (
    <motion.section
      className="min-h-screen bg-white text-gray-900"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      {/* Navigation */}
      <div className="sticky top-0 z-50 bg-black">
        <Navigation />
      </div>

      {/* Hero */}
      <motion.header
        className="px-6 py-16 text-center bg-gradient-to-br from-black to-gray-900 text-white"
        initial={{ opacity: 0, y: -30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, ease: "easeOut" }}
      >
        <h1 className="text-4xl md:text-6xl font-extrabold tracking-tight">
          Privacy Policy
        </h1>
        <p className="mt-4 text-lg md:text-xl text-gray-300">
          Your privacy is our priority. No tracking. No selling. No nonsense.
        </p>
        <div className="h-1 w-24 mx-auto mt-6 bg-white rounded-full" />
      </motion.header>

      {/* Main Content */}
      <main className="px-6 py-12 md:px-16 lg:px-32 max-w-6xl mx-auto space-y-12 text-gray-800">
        {sections.map((section, index) => (
          <div
            key={section.title}
            ref={(el) => (sectionRefs.current[index] = el)}
          >
            <Section icon={section.icon} title={section.title}>
              {section.content}
            </Section>
          </div>
        ))}
      </main>

      <ChatWidget />
    </motion.section>
  );
}

// Section Component
function Section({ icon, title, children }) {
  return (
    <div className="bg-white border-l-4 border-black p-6 rounded-lg shadow-md hover:shadow-lg transition">
      <div className="flex items-center gap-3 mb-4">
        {icon}
        <h2 className="text-2xl font-bold">{title}</h2>
      </div>
      <div className="text-gray-700 leading-relaxed">{children}</div>
    </div>
  );
}

